import { Controller } from '@ali/4ever-cangjie';
import {
  UPDATE_HOVER_HIGHLIGHT,
  REMOVE_HOVER_HIGHLIGHT,
} from './actions';

import updateHighlight from './commands/updateHighlight';
import removeHighlight from './commands/removeHighlight';
import type { IHoverBlockConfig } from './types';

export default function createOnAction(config: IHoverBlockConfig) {
  return (action, controller: Controller, next) => {
    const { type, payload } = action;
    const { isHighlightBorder = false } = config;
    if (type === UPDATE_HOVER_HIGHLIGHT) {
      updateHighlight(controller, payload, isHighlightBorder);
      return controller;
    }
    if (type === REMOVE_HOVER_HIGHLIGHT) {
      removeHighlight(controller, payload, isHighlightBorder)
      return controller;
    }
    return next();
  }
}